@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  height: 100%;
}
body {
  min-height: 100%;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  outline: 0;
  outline-offset: 0;
}
.wrapper {
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .wrapper {
    max-width: 1440px;
  }
}
.page-header {
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .page-header--nojs {
    flex-direction: column;
    align-items: stretch;
  }
  .page-header--nojs .main-navigation__toggle {
    display: none;
  }
  .page-header--nojs .main-navigation__list {
    position: static;
    display: block;
  }
}
.page-header--border-bottom {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .page-header--border-bottom {
    border: none;
  }
}
.page-header__wrapper {
  padding: 13px 20px 14px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    padding: 25px 30px;
    align-items: flex-start;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    padding: 55px 110px 29px 110px;
  }
}
.page-header__logo {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px 56px;
  transition: opacity 0.3s ease;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header__logo {
    gap: 13px;
  }
}
.page-header__logo-image {
  display: block;
}
@media (min-width: 768px) {
  .page-header__navigation {
    align-self: flex-end;
  }
}
.main-navigation__toggle {
  display: block;
  width: 24px;
  height: 24px;
  border: none;
  padding: 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation__toggle svg {
  stroke: #000000;
  transition: stroke 0.3s ease;
}
.main-navigation__toggle:hover svg,
.main-navigation__toggle:focus-visible svg {
  stroke: #68B738;
}
.main-navigation__toggle:active svg {
  stroke: rgba(104, 183, 56, 0.3);
}
.main-navigation__toggle--default .main-navigation__toggle-hamburger {
  display: block;
}
.main-navigation__toggle--default .main-navigation__toggle-cross {
  display: none;
}
.main-navigation__toggle--active .main-navigation__toggle-hamburger {
  display: none;
}
.main-navigation__toggle--active .main-navigation__toggle-cross {
  display: block;
}
.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 65px;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: #ffffff;
}
.main-navigation__list--closed {
  display: none;
}
.main-navigation__list--opened {
  display: block;
}
@media (min-width: 768px) {
  .main-navigation__list {
    position: relative;
    top: 0;
    max-width: 450px;
    display: flex;
    flex-wrap: wrap;
    gap: 33px;
    z-index: 1;
    background-color: transparent;
  }
}
@media (min-width: 1440px) {
  .main-navigation__list {
    max-width: 600px;
  }
}
.main-navigation__item {
  border-top: 1px solid #e6e6e6;
}
.main-navigation__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-navigation__item:last-child {
    border: none;
  }
}
@media (min-width: 768px) {
  .main-navigation__item {
    border: none;
  }
}
.main-navigation__link {
  display: block;
  padding: 22px 0 24px;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
  transition: opacity 0.3s ease;
}
@media (min-width: 768px) {
  .main-navigation__link--active {
    border-bottom: 2px solid #68B738;
  }
}
@media (min-width: 1440px) {
  .main-navigation__link--active.main-navigation__link--has-background {
    border-bottom-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-navigation__link {
    padding: 10px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .main-navigation__link {
    font-size: 20px;
    line-height: 30px;
  }
  .main-navigation__link--has-background {
    color: #ffffff;
  }
}
.main-navigation__link:hover {
  opacity: 0.6;
}
.main-navigation__link:active {
  opacity: 0.3;
}
.title {
  margin: 0;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 36px;
}
.title--main {
  color: #000000;
  line-height: 40px;
}
@media (min-width: 768px) {
  .title--main {
    font-size: 60px;
    line-height: 60px;
  }
}
.title--extra {
  color: #ffffff;
  line-height: 36px;
}
@media (min-width: 768px) {
  .title--extra {
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}
.button {
  cursor: pointer;
  border: none;
  padding: 10px 0;
  font-family: "Oswald", "Tahoma", sans-serif;
  text-transform: uppercase;
  display: block;
  text-align: center;
}
.button--main {
  background-color: #68B738;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.button--main:hover {
  background-color: #5eaa2f;
}
.button--main:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button--main {
    padding: 14px 0;
    font-size: 20px;
    line-height: 26px;
  }
}
.button--product {
  font-size: 16px;
  line-height: 20px;
  background-color: #68B738;
  color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.button--product:hover {
  background-color: #5eaa2f;
}
.button--product:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--show-more {
  font-size: 16px;
  line-height: 20px;
  background-color: #f2f2f2;
  color: #444444;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.button--show-more:hover {
  background-color: #ebebeb;
}
.button--show-more:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}
.promo {
  position: relative;
  z-index: 1;
  background-image: url("../img/promo/promo-background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: bottom 188px center;
  background-size: cover;
  background-color: #ffffff;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo/promo-background-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .promo {
    background-image: none;
  }
}
@media (min-width: 1440px) {
  .promo {
    z-index: 0;
  }
}
@media (min-width: 1440px) {
  .promo::before {
    content: "";
    width: 50%;
    height: calc(100% + 143px);
    position: absolute;
    right: 0;
    top: -143px;
    background-image: url("../img/promo/promo-background-desktop.jpg");
    background-size: cover;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/promo/promo-background-desktop@2x.jpg");
  }
}
