  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 27px 20px 10px 20px;
}
@media (min-width: 768px) {
  .promo__wrapper {
    padding: 40px 120px 526px;
    align-items: flex-start;
  }
}
@media (min-width: 1440px) {
  .promo__wrapper {
    padding: 81px 190px 184px;
  }
}
.promo__title {
  margin-bottom: 25px;
  text-align: center;
}
@media (min-width: 768px) {
