    margin-bottom: 52px;
  }
}
.promo__image {
  display: block;
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .promo__image {
    margin: 0;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
}
@media (min-width: 1440px) {
  .promo__image {
    top: 0;
    left: auto;
    right: 332px;
    z-index: 0;
    transform: none;
  }
}
.promo__link {
  align-self: stretch;
}
@media (min-width: 768px) {
  .promo__link {
    z-index: 1;
    align-self: flex-start;
    width: 244px;
