.guide-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
  counter-reset: section;
}
@media (min-width: 768px) {
  .guide-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 37px 60px;
  }
}
@media (min-width: 1440px) {
