.guide-list__item {
  position: relative;
  display: flex;
  align-items: center;
  z-index: 1;
  gap: 20px;
}
@media (min-width: 768px) {
  .guide-list__item {
    padding: 40px 26px 57px 0;
    flex-direction: column;
    gap: 31px;
    align-items: flex-start;
  }
  .guide-list__item:first-child::before {
    right: 100px;
  }
  .guide-list__item::before {
    position: absolute;
    top: 0;
    right: 82px;
    z-index: -1;
    counter-increment: section;
    content: counters(section, "");
    font-family: "Oswald", "Tahoma", sans-serif;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .guide-list__item:first-child::before {
    right: 22px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .guide-list__item::before {
    right: 0;
  }
}
